:root {
    --danger: #ff8a80;
    --blue: #6292db;
    --light: #008080;
  }
  
  body {
    background-color: #008080;
    background-color: var(--light);
  }
* {
    margin: 0;
    padding: 0;
    font-family: ubuntu;
    list-style: none;
  }
.error {
    background-color: var(--danger);
    text-align: center;
    color: white;
    width: 70%;
    margin: 1rem auto;
    padding: 0.5rem 0;
    border-radius: 0.5rem;
    box-shadow: 5px;
}
.navbar {
    background-color: #436363;
    padding: 1rem 0;
    border-radius: 15px;
    
  }
  
  .navbar-brand {
    background-color: whitesmoke;
    color: var(--blue);
    text-decoration: none;
    padding: 0.5rem;
    border-radius: 0.5rem;
    text-transform: uppercase;
    font-weight: bold;
  }
  
  .navbar-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
  
  .navbar-item {
    color: white;
    text-decoration: none;
    cursor: pointer;
  }
  
  .navbar-user {
    margin-top: 0.5rem;
    text-align: right;
    color: black;
  }

.form {
    width: 60%;
    margin: auto;
    padding: 20px;
    border-radius: 25px;
    
  }
  
  .form-input {
    width: 100%;
    padding: 0.5rem 1rem;
    margin: 0.5rem 0;
    border: 1px solid var(--light);
    border-radius: 0.4rem;
    box-sizing: border-box;
    outline: none;
    font-size: 1rem;
  }
  
  .form-label {
    color: white;
    text-transform: uppercase;
    font-size: 0.8rem;
  }
.button {
    background-color: white;
    border: 2px solid var(--bue);
    border-radius: 0.5rem;
    color: var(--light);
    font-size: 1rem;
    margin: 1rem;
    padding: 0.2rem 1rem;
    -webkit-transition: all 0.4s ease-out;
    transition: all 0.4s ease-out;
  }
  
  .button:hover {
    background-color: rgb(121, 184, 124);
  }
  
  .buttons_center {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
  }
.polls li {
    background-color: var(--light);
    color: rgb(185, 158, 158);
    padding: 0.5rem;
    text-align: center;
  }
  
  .polls li:nth-child(odd) {
    background-color: white;
  }
  
  .polls li:hover {
    border: 2px solid var(--blue);
  }
  
  .poll-title {
    color: black;
    font-size: 2rem;
    margin-top: 1rem;
    text-align: center;
    text-transform: uppercase;
  }

  .pie {
    width: 40%;
    -webkit-align-content: center;
            align-content: center;

  }
/* The side navigation menu */
.sidebar {
    margin: 0;
    padding: 0;
    width: 100px;
    background-color: #7ec5b3;
    position: fixed;
    height: 80%;
    overflow: auto;
    border-radius: 20px;
  }
  
  /* Sidebar links */
  .sidebar a {
    color: black;
    padding: 0px;
    text-decoration: none;
    border-radius: 10px;
  }
  
  /* Active/current link */
  .sidebar a.active {
    background-color: rgb(109, 133, 156);
    color: white;
  }
  
  /* Links on mouse-over */
  .sidebar a:hover:not(.active) {
    background-color: #555;
    color: white;
  }
  
  /* Page content. The value of the margin-left property should match the value of the sidebar's width property */
  div.content {
    margin-left: 200px;
    padding: 0px;
    height: 1000px;
  }
  
  /* On screens that are less than 700px wide, make the sidebar into a topbar */
  @media screen and (max-width: 700px) {
    .sidebar {
      width: 100%;
      height: auto;
      position: absolute;
    }
    .sidebar a {float: left;}
    div.content {margin-left: 1px;}
  }
  
  /* On screens that are less than 400px, display the bar vertically, instead of horizontally */
  @media screen and (max-width: 400px) {
    .sidebar a {
      text-align: center;
      float: none;
    }
  }

  /* Dropdown Button */
.dropbtn {
  background-color: #3498DB;
  color: white;
  padding: 16px;
  font-size: 16px;
  border: none;
  cursor: pointer;
}

/* Dropdown button on hover & focus */
.dropbtn:hover, .dropbtn:focus {
  background-color: #2980B9;
}

/* The container <div> - needed to position the dropdown content */
.dropdown {
  position: relative;
  display: inline-block;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

/* Links inside the dropdown */
.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

/* Change color of dropdown links on hover */
.dropdown-content a:hover {background-color: #ddd}

/* Show the dropdown menu (use JS to add this class to the .dropdown-content container when the user clicks on the dropdown button) */
.show {display:block;}
